import amount from "@/components/shopping/amount.vue"
import uniPopup from "@/components/shopping/uni-popup.vue"
export default{
	data(){
		return{
			// numberValue:1,
			phoneHeight:0,
			show: false,
			type: '',
			content: '顶部弹 popup',
			xzindex:0
		}
	},
	props:['djbj'],
	components:{
		amount,
		uniPopup
	},
	methods:{
		// change(value) {
		// 	// this.numberValue = value
		// 	console.log("商品数量"+value)
		// },
		change(e) {
			console.log(e.show)
		},
		//编辑弹出框
		togglePopup(type, open) {
			switch (type) {
				case 'bottom':
					this.content = '底部弹出 popup'
					break
			}
			this.type = type
			if (open === 'tip') {
				this.show = true
			} else {
				this.$refs[open].open()
			}
		},
		// 点击选中
		xz(index){
			console.log(index)
			this.xzindex=index
		}
	},
	created(){
		
	}
}
